.services {
	margin-top: 60px;
	.flex__services {
		margin-top: 30px;
		margin-inline: auto;
		display: flex;
		flex-direction: column;
		align-items: center;
		row-gap: 25px;
		.service {
			width: 348px;
			text-align: center;
			padding-block: 20px;
			.image__container {
				border-radius: 50%;
				height: 270px;
				width: 270px;
				padding: 45px;
				border: 1px solid var(--first-color-base);
				margin: auto;
				display: flex;
				align-items: center;
				justify-content: center;
			}
			&__title {
				font-size: 1.75rem;
				font-family: var(--body-font);
				font-weight: var(--font-bold);
				color: var(--first-color-base);
				margin-top: 25px;
				margin-bottom: 20px;
			}
			&__paragraph {
				font-size: 19px;
				line-height: 26px;
				letter-spacing: 0.02em;
				font-family: var(--paragraph-font);
			}
		}
	}
}
